import React, { useState, Fragment, useEffect } from "react";
import clsx from "clsx";
import { makeStyles, useTheme, ThemeProvider } from "@material-ui/core/styles";
import { StoreProvider, useStoreContext } from "./utils/store";
import AppBar from "./components/AppBar";
import WorkExperience from "./components/WorkExperience";
import ProjectExperience from "./components/Project";
import Skill from "./components/Skill";
import globalTheme from "./globalTheme";
import { Divider, CssBaseline, Grid, useMediaQuery } from "@material-ui/core";
import { Card } from "./components/common";
import {
  ContactInfo,
  Education,
  PersonalRecognition,
  PersonalInfo,
} from "./components/PersonalInfo";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flex: 1,
  },
  vertical: {
    backgroundColor: theme.palette.primary.main,
  },
  drawerHeaderPlaceholder: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    padding: theme.spacing(2),
    marginLeft: -theme.drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
}));

const App = function (props) {
  const theme = useTheme();
  const { dispatch } = useStoreContext();
  const classes = useStyles(theme);

  // 是否横屏
  const isLandscape = useMediaQuery(
    [
      `${theme.breakpoints.up("md")} and (orientation: portrait)`,
      `${theme.breakpoints.up("sm")} and (orientation: landscape)`,
    ].reduce((finalV, query) => `${finalV},${query.replace(/@media\s*/g, "")}`)
  );

  useEffect(() => {
    dispatch({ 
      type: "UPDATE",
      payload: {
        isLandscape
      }
    })
  }, [isLandscape, dispatch]);
  
  const [isDrawerOpen, setIsDrawerOpen] = useState(true); // 抽屉开关

  const handleDrawerOpen = () => {
    setIsDrawerOpen(true);
  };

  const handleDrawerClose = () => {
    setIsDrawerOpen(false);
  };

  // 横屏
  if (isLandscape)
    return (
      <div className={classes.root}>
        <CssBaseline />
        <AppBar
          isDrawerOpen={isDrawerOpen}
          handleDrawerOpen={handleDrawerOpen}
          handleDrawerClose={handleDrawerClose}
        >
          {[
            <PersonalInfo />,
            <ContactInfo />,
            <Education />,
            <PersonalRecognition />,
          ].map((JSX, index, arr) => (
            <Fragment key={JSX.type.name}>
              {JSX}
              {index < arr.length - 1 ? <Divider /> : null}
            </Fragment>
          ))}
        </AppBar>
        <main
          className={clsx(classes.content, {
            [classes.contentShift]: isDrawerOpen,
          })}
        >
          <div className={classes.drawerHeaderPlaceholder} />
          <Grid container direction="column" spacing={1}>
            <Grid item>
              <WorkExperience />
            </Grid>
            <Grid item>
              <ProjectExperience />
            </Grid>
            <Grid item>
              <Skill />
            </Grid>
          </Grid>
        </main>
      </div>
    );
  // 竖屏
  else
    return (
      <div className={clsx(classes.root, classes.vertical)}>
        <CssBaseline />
        <main
          className={clsx(classes.content, {
            [classes.contentShift]: isDrawerOpen,
          })}
        >
          <Grid container direction="column" spacing={1}>
            <Grid item>
              <Card>
                <PersonalInfo />
                <ContactInfo />
              </Card>
            </Grid>
            <Grid item>
              <WorkExperience />
            </Grid>
            <Grid item>
              <ProjectExperience />
            </Grid>
            <Grid item>
              <Skill />
            </Grid>
            <Grid item>
              <Card>
                <Education />
                <PersonalRecognition />
              </Card>
            </Grid>
          </Grid>
        </main>
      </div>
    );
};

export default function AppWrapper() {
  return (
    <ThemeProvider theme={globalTheme}>
      <StoreProvider>
        <App />
      </StoreProvider>
    </ThemeProvider>
  );
}
