/**
 * 计算年龄、工龄等
 * @param {Date} startDate
 * @returns
 */
export default function getAge(startDate) {
  const birthday = new Date(startDate);
  const now = new Date();
  let diff = 1;

  if (
    now.getMonth() >= birthday.getMonth() &&
    now.getDate() >= birthday.getDate()
  ) {
    diff = 0;
  }
  return now.getFullYear() - birthday.getFullYear() - diff;
}
