export default memo(function () {
  const [chipList, setChipList] = useState(initialChipList);

  useLayoutEffect(() => {
    const chipMaxWidth = Math.max(
      ...Array.from(document.querySelectorAll(".js-chip")).map(
        el => el.getBoundingClientRect().width
      )
    );

    setChipList(
      chipList.map(({ avatarStyle, chipStyle, descStyle, ...rest }) => ({
        ...rest,
        useStyles: makeChipStyles({
          chipMaxWidth,
          avatarStyle,
          chipStyle,
          descStyle,
        }),
      }))
    );
  }, []);

  return (
    <Card>
      <CardHeader>技能</CardHeader>
      <List>
        {chipList.map((item, i) => (
          <CustomListItem key={item.label} {...item} />
        ))}
      </List>
    </Card>
  );
});

const CustomListItem = memo(({ label, avatar, desc, useStyles }) => {
  const styles = useStyles();

  return (
    <ListItem className={styles.item}>
      <Grid container>
        <Grid item container alignItems="center" className={styles.gridWrapper}>
          <Chip
            // avatar={
            //   avatar
            //     ? <Avatar
            //       src={avatar}
            //       classes={{ root: styles.avatarStyle }}
            //     />
            //     : undefined
            // }
            variant="outlined"
            size="small"
            label={label}
            classes={{ root: styles.chipStyle }}
            className="js-chip"
          />
        </Grid>
        <Grid
          item
          container
          alignItems="center"
          className={clsx(styles.gridWrapper, styles.descStyle)}
        >
          <Typography component="div">{desc}</Typography>
        </Grid>
      </Grid>
    </ListItem>
  );
});
