import React, { useState, useMemo, memo, useRef, useLayoutEffect } from "react";
import clsx from "clsx";
import {
  Divider,
  CardContent,
  Typography,
  Grid,
  Collapse,
  Box,
} from "@material-ui/core";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import {
  Timeline,
  TimelineDot,
  TimelineConnector,
  TimelineSeparator,
  TimelineContent,
  TimelineItem,
} from "@material-ui/lab";
import { ExpandLess, ExpandMore } from "@material-ui/icons";

import { Card, CardHeader, TextOrderList } from "../common";
import getAge from "../../utils/getAge";
import { useStoreContext } from "../../utils/store";

const CustomCardContent = withStyles(
  (theme) => ({
    root: {
      [theme.breakpoints.down("sm")]: {
        padding: "unset",
        "&:last-child": {
          paddingBottom: "unset",
        },
      },
      paddingTop: "unset",
      paddingBottom: "unset",
    },
  }),
  { name: "CardContent" }
)(CardContent);

const CustomTimelineItem = withStyles(
  {
    root: {
      minHeight: "unset",
    },
    missingOppositeContent: {
      "&::before": {
        content: "unset",
      },
    },
  },
  { name: "MuiTimelineItem" }
)(TimelineItem);

const CustomTimelineDot = withStyles({
  root: {
    marginTop: "11px",
  },
})(TimelineDot);

const CustomTimelineConnector = withStyles(
  (theme) => ({
    root: {
      backgroundColor: theme.palette.primary.main,
    },
  }),
  { name: "MuiTimelineConnector" }
)(TimelineConnector);

const useCollapseTitleStyles = makeStyles((theme) => {
  return {
    title: {
      cursor: "pointer",
    },
    titleWrapper: {
      alignItems: "flex-start",
    },
    date: {
      marginRight: "2em",
    },
    divider: {
      margin: theme.spacing(2, 0),
    },
    titleText: {
      marginRight: theme.spacing(2),
      lineHeight: "1.5rem",
    },
    titleIcon: {
      lineHeight: 0,
      marginLeft: "auto",
    },
  };
});

const CollapseTitle = (props) => {
  const { isLandscape } = useStoreContext();
  const [isOpen, setIsOpen] = useState(true);
  const classes = useCollapseTitleStyles();
  const { date, title, maxDateWidth, setMaxDateWidth, children } = props;
  const dateRef = useRef();

  useLayoutEffect(() => {
    if (dateRef.current) {
      const dateWidth = Number.parseFloat(
        window.getComputedStyle(dateRef.current).width.replace(/[^\d.]*/g, "")
      );

      setMaxDateWidth((maxDateWidth) => {
        if (!maxDateWidth || dateWidth > maxDateWidth) {
          return dateWidth;
        }
        return maxDateWidth;
      });
    }
  }, [maxDateWidth, setMaxDateWidth]);

  return (
    <Box className={classes.title} onClick={() => setIsOpen(!isOpen)}>
      <Grid
        container
        justifyContent="flex-start"
        alignItems="center"
        direction="row"
        className={classes.titleWrapper}
      >
        {isLandscape ? (
          <>
            <Grid
              item
              className={classes.date}
              ref={dateRef}
              {...(maxDateWidth && {
                style: {
                  width: `${maxDateWidth}px`,
                },
              })}
            >
              <Typography noWrap color="textSecondary">
                {date}
              </Typography>
            </Grid>
            <Grid
              sm
              item
              container
              justifyContent="flex-start"
              alignItems="center"
              direction="row"
              wrap="nowrap"
            >
              <Grid item className={classes.titleText}>
                <Typography color="primary">{title}</Typography>
              </Grid>
            </Grid>
            <Grid item className={classes.titleIcon}>
              {isOpen ? <ExpandLess /> : <ExpandMore />}
            </Grid>
          </>
        ) : (
          <>
            <Grid
              item
              className={classes.date}
              ref={dateRef}
              {...(maxDateWidth && {
                style: {
                  width: `${maxDateWidth}px`,
                },
              })}
            >
              <Typography noWrap color="textSecondary">
                {date}
              </Typography>
            </Grid>
            <Grid item className={classes.titleIcon}>
              {isOpen ? <ExpandLess /> : <ExpandMore />}
            </Grid>
            <Grid
              sm
              item
              container
              justifyContent="flex-start"
              alignItems="center"
              direction="row"
              wrap="nowrap"
            >
              <Grid item className={classes.titleText}>
                <Typography color="primary">{title}</Typography>
              </Grid>
            </Grid>
          </>
        )}
      </Grid>
      <Collapse timeout="auto" in={isOpen} className={classes.content}>
        {children}
      </Collapse>
    </Box>
  );
};

const WorkExperience = memo(function WorkExperience() {
  const workAge = getAge(new Date("2019-07-01"));
  const experienceList = useMemo(
    () => [
      {
        title: "广州力挚网络科技有限公司（租租车）- 前端开发工程师",
        date: "2019.7-至今",
        children: (
          <TextOrderList>
            <Typography>租租车国内租车业务的web页面开发和维护</Typography>
            <Typography>
              租租车国内租车业务的微信、支付宝、百度等平台小程序的开发和维护
            </Typography>
            <Typography>租租车国内租车业务的管理后台的开发和维护</Typography>
          </TextOrderList>
        ),
      }
    ],
    []
  );

  // 任职日期最大长度
  const [maxDateWidth, setMaxDateWidth] = useState(null);

  return (
    <Card>
      <CardHeader>工作经历({workAge}年)</CardHeader>
      {/* <CustomCardContent> */}
      <Timeline align="left">
        {experienceList.map(({ date, title, children }, i) => (
          <CustomTimelineItem key={date}>
            <TimelineSeparator>
              <CustomTimelineDot color="primary" />
              {i < experienceList.length - 1 ? (
                <CustomTimelineConnector />
              ) : null}
            </TimelineSeparator>
            <TimelineContent>
              <CollapseTitle
                date={date}
                title={title}
                maxDateWidth={maxDateWidth}
                setMaxDateWidth={setMaxDateWidth}
              >
                {children}
              </CollapseTitle>
            </TimelineContent>
          </CustomTimelineItem>
        ))}
      </Timeline>
      {/* </CustomCardContent> */}
    </Card>
  );
});

export default WorkExperience;
