import React, { createContext, useReducer, useContext } from "react";

// 创建 context
export const StoreContext = createContext({});

const reducer = (state, action) => {
  switch (action.type) {
    case "UPDATE":
      return {
        ...state,
        ...action.payload,
      };
    default:
      return state;
  }
};

export const StoreProvider = (props) => {
  const [storeState, dispatch] = useReducer(reducer, { ...props.state });

  return (
    <StoreContext.Provider value={{ ...storeState, dispatch }}>
      {props.children}
    </StoreContext.Provider>
  );
};

export function StoreConsumer(props) {
  return <StoreContext.Consumer>{props.children}</StoreContext.Consumer>;
}

export function useStoreContext() {
  return useContext(StoreContext);
}
